import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';

@Component({
  selector: 'app-bookkeeping',
  templateUrl: './bookkeeping.component.html',
  styleUrls: ['./bookkeeping.component.scss']
})
export class BookkeepingComponent implements OnInit {

  constructor(private meta: Meta, private title: Title) {
    //
  }

  ngOnInit() {
    this.setMetaTags();
  }

  setMetaTags() {
    this.title.setTitle("Online Bookkeeping Service - NOAC Consultants")
    this.meta.updateTag(
      {
        name: "description",
        content:
          "Online Bookkeeping Service from Kite. Find out how it works, how it helps business owners like you. Our solution aims to save you time, money and improve your bookkeeping practice."
      },
      // 'name="description"'
    )
  }

}
