import { Component, OnInit } from '@angular/core';
import { SignupService } from '../../../_services';
import { first } from 'rxjs/operators';
import { Plan } from '../../../_models';

@Component({
  selector: 'app-bookkeeping-packages',
  templateUrl: './bookkeeping-packages.component.html',
  styleUrls: ['./bookkeeping-packages.component.scss']
})
export class BookkeepingPackagesComponent implements OnInit {

  stripePackages: Plan[] = [];

  constructor(
    private signupService: SignupService
    ) { }

  ngOnInit() {
    this.signupService
      .getPackages()
      .pipe(first())
      .subscribe(plans => {
        console.log('plans ', plans)
        this.stripePackages = plans["data"];
      });
  }

}
