import { Component, OnInit, ViewChild } from "@angular/core"
import { NgForm } from "@angular/forms"
import { ContactUsService, SnackBarService } from "../../../_services"
import { first } from "rxjs/operators"
import { Meta, Title } from "@angular/platform-browser"

@Component({
  selector: "app-contact",
  templateUrl: "./contact.component.html",
  styleUrls: ["./contact.component.scss"]
})
export class ContactComponent implements OnInit {
  contactForm = this.contactUsService.init()

  loading = false
  submitted = false

  @ViewChild("contactFormDirective") private contactFormDirective: NgForm

  constructor(
    private contactUsService: ContactUsService,
    private snackBarService: SnackBarService,
    private meta: Meta,
    private title: Title
  ) {
    //
  }

  ngOnInit() {
    this.setMetaTags()
  }

  setMetaTags() {
    this.title.setTitle("Contact Us - NOAC Consultants")
    this.meta.updateTag(
      {
        name: "description",
        content:
          "Contact NOAC Consultants. For questions or request for more information, send us an inquiry today."
      },
      // 'name="description"'
    )
  }

  // convenience getter for easy access to form fields
  get f() {
    return this.contactForm.controls
  }

  async onSubmit() {
    this.submitted = true

    // stop here if form is invalid
    if (this.contactForm.invalid) {
      return
    }

    this.loading = true

    this.contactUsService
      .send(this.contactForm.value)
      .pipe(first())
      .subscribe(
        res => {
          this.snackBarService.openSnackBar(res.message, "", null, "", "top")
          this.loading = false
          this.contactFormDirective.resetForm()
        },
        error => {
          console.log("error ", error)
          this.loading = false
        }
      )
  }
}
