import { Injectable } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { CARD_BRANDS } from "../_mock";

import { HttpClient } from "@angular/common/http";
import { map } from "rxjs/operators";
import { environment } from '../../environments/environment';
import { BehaviorSubject, Observable } from "rxjs";

@Injectable({
  providedIn: "root"
})
export class SignupService {
  private url = environment.APIEndpoint;

  private strongRegex = new RegExp(
    "^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*])(?=.{6,})"
  );

  organizationFormGroup: FormGroup = this.formBuilder.group(
    {
      stripeToken: [""],
      token: [""],
      planPackage: [""],
      package_name: [""],
      couponCode: [""],
      password: [
        "",
        [
          Validators.required,
          Validators.minLength(6),
          Validators.pattern(this.strongRegex)
        ]
      ],
      password_confirmation: [""],
      organization: this.formBuilder.group({
        acn: [
          "",
          [
            // Validators.required,
            Validators.minLength(9),
            Validators.maxLength(9),
            Validators.pattern("^[0-9]*$")
          ]
        ],
        abn: [
          "",
          [
            // Validators.required,
            Validators.minLength(11),
            Validators.maxLength(11),
            Validators.pattern("^[0-9]*$")
          ]
        ],
        name: ["", [Validators.required]],
        address: this.formBuilder.group({
          apt_building_floor_etc: [""],
          street: this.formBuilder.group({
            number: ["", [Validators.required]],
            name: ["", [Validators.required]],
            type: ["", [Validators.required]]
          }),
          city: ["", [Validators.required]],
          state_province_region: ["", [Validators.required]]
        }),
        timezone: ["", [Validators.required]],
        currency: ["Australian Dollar", [Validators.required]],
        nature: ["", [Validators.required]]
      })
    },
    { validator: this.checkPasswords }
  );

  cardBrandToPfClass = CARD_BRANDS;

  private couponCodeSubject: BehaviorSubject<Array<any>>;
  public couponCode: Observable<Array<any>>;

  constructor(private formBuilder: FormBuilder, private http: HttpClient) {
    this.couponCodeSubject = new BehaviorSubject<[]>([]);
    this.couponCode = this.couponCodeSubject.asObservable();
  }

  initOrganizationFormStep() {
    this.organizationFormGroup = this.formBuilder.group(
      {
        password: [
          "",
          [
            Validators.required,
            Validators.minLength(6),
            Validators.pattern(this.strongRegex)
          ]
        ],
        password_confirmation: [""],
        organization: this.formBuilder.group({
          name: ["", [Validators.required]],
          pay_its_taxes: ["", [Validators.required]],
          timezone: ["", [Validators.required]],
          currency: ["Australian Dollar", [Validators.required]],
          nature: ["", [Validators.required]],
          financial_year_end: ["", [Validators.required]]
        })
      },
      { validator: this.checkPasswords }
    );
  }

  checkPasswords(group: FormGroup) {
    let pass = group.controls.password.value;
    let confirmPass = group.controls.password_confirmation.value;

    return pass === confirmPass ? null : { notSame: true };
  }

  setBrandIcon(brand) {
    var brandIconElement = document.getElementById("brand-icon");
    var pfClass = "pf-credit-card";
    if (brand in this.cardBrandToPfClass) {
      pfClass = this.cardBrandToPfClass[brand];
    }
    for (var i = brandIconElement.classList.length - 1; i >= 0; i--) {
      brandIconElement.classList.remove(brandIconElement.classList[i]);
    }
    brandIconElement.classList.add("pf");
    brandIconElement.classList.add(pfClass);
  }

  register(data: object) {
    return this.http.post<any>(this.url + `/auth/register`, data).pipe(
      map(user => {
        return user;
      })
    );
  }

  createSubscription(data: object) {
    return this.http.post<any>(this.url + `/stripe/subscriptions`, data);
  }

  checkToken(token: string) {
    return this.http.post<any>(this.url + `/stripe/subscriptions/check-token`, {
      token: token
    });
  }

  validateEmail(email) {
    return this.http.post<any>(this.url + `/auth/validate/email`, {
      email: email
    });
  }

  validateCouponCode(code) {
    return this.http
      .post<any>(this.url + `/auth/validate/coupon-code`, {
        coupon_code: code
      })
      .pipe(map(data => {
        if (data.valid) {
          this.couponCodeSubject.next(data);
        } else {
          this.couponCodeSubject.next(null);
        }
        return data;
      }));
  }

  getPackages() {
    return this.http.get(this.url + `/stripe/plans`);
  }

  clearCouponCode() {
    this.couponCodeSubject.next(null);
  }
}
