import { FormControl } from "@angular/forms";
import { SignupService } from "../_services";
import { Injectable } from "@angular/core";

@Injectable()
export class CouponCodeValidator {
  debouncer: any;

  constructor(public signupService: SignupService) {}

  checkCouponCode(control: FormControl): any {
    clearTimeout(this.debouncer);

    if (!control.value) {
      return new Promise(resolve => {
        resolve(null);
      });
    }

    return new Promise(resolve => {
      this.debouncer = setTimeout(() => {
        this.signupService.validateCouponCode(control.value).subscribe(
          res => {
            if (res.valid) {
              resolve(null);
            } else {
              resolve({ couponCodeExpired: true });
            }
          },
          err => {
            resolve({ couponCodeInvalid: true });
          }
        );
      }, 1000);
    });
  }
}
