import { Component, OnInit, ViewChild, Input } from "@angular/core";
import { SnackBarService, SignupService } from "../../_services";
import { FormBuilder, NgForm, Validators, FormGroup } from "@angular/forms";
import { CustomEmailValidator } from "../../_validators";
import { first } from "rxjs/operators";

@Component({
  selector: "app-sign-up-form2",
  templateUrl: "./sign-up-form2.component.html",
  styleUrls: ["./sign-up-form2.component.scss"]
})
export class SignUpForm2Component implements OnInit {
  registerForm: FormGroup;
  loading = false;
  submitted = false;

  @ViewChild("registerFormDirective") private registerFormDirective: NgForm;
  @Input() layout: string;

  constructor(
    private formBuilder: FormBuilder,
    private signupService: SignupService,
    private snackBarService: SnackBarService,
    public emailValidator: CustomEmailValidator
  ) {}

  ngOnInit() {
    this.registerForm = this.formBuilder.group({
      name: ["", [Validators.required]],
      subject: ["", [Validators.required]],
      message: ["", [Validators.required]],
      email: [
        "",
        [Validators.required, Validators.email],
        this.emailValidator.checkEmail.bind(this.emailValidator)
      ],
      phone_number: [
        "",
        [
          Validators.required,
          Validators.maxLength(12),
          Validators.pattern("^[0-9+]*$")
        ]
      ]
    });
  }

  // convenience getter for easy access to form fields
  get f() {
    return this.registerForm.controls;
  }

  async onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.registerForm.invalid) {
      return;
    }

    this.loading = true;

    this.signupService
      .register(this.registerForm.value)
      .pipe(first())
      .subscribe(
        res => {
          console.log("res ", res);
          this.snackBarService.openSnackBar(res.message, "", null, "", "top");
          this.loading = false;
          this.registerFormDirective.resetForm();
        },
        error => {
          console.log("error ", error);
          // if (error.error.error) {
          //   this.alertService.error(error.error.error, "danger");
          // } else {
          //   this.alertService.serverErrors(error.error.errors);
          // }
          this.loading = false;
        }
      );
  }
  keyPress(event: any) {
    const pattern = /[0-9\+\-\ ]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
}
