import { Component, OnInit } from "@angular/core"
import { SignupService } from "../../../_services"
import { first } from "rxjs/operators"
import { CustomEmailValidator } from "../../../_validators"
import { Plan } from "../../../_models"
import { Meta, Title } from "@angular/platform-browser"

@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.scss"]
})
export class HomeComponent implements OnInit {
  stripePackages: Plan[] = []

  constructor(
    private signupService: SignupService,
    public emailValidator: CustomEmailValidator,
    private meta: Meta,
    private title: Title
  ) {
    // title.setTitle("NOAC Consultants - Online Bookkeeping Service")
    // meta.updateTag(
    //   {
    //     name: "description",
    //     content:
    //       "Welcome to Kite. We are an online bookkeeping solution to businesses who are in need of a bookkeeper. We combine technology and expert financial handling to manage your books day to day."
    //   },
    // )
    // meta.updateTag(
    //   {
    //     content:
    //       "Welcome to Kite. We are an online bookkeeping solution to businesses who are in need of a bookkeeper. We combine technology and expert financial handling to manage your books day to day."
    //   },
    //   'name="description"'
    // )
  }

  async ngOnInit() {
    await this.setMetaTags()

    await this.signupService
      .getPackages()
      .pipe(first())
      .subscribe(plans => {
        this.stripePackages = plans["data"]
      })
  }

  setMetaTags() {
    this.title.setTitle("NOAC Consultants - Online Bookkeeping Service")
    this.meta.updateTag(
      {
        name: "description",
        content:
          "Welcome to Kite. We are an online bookkeeping solution to businesses who are in need of a bookkeeper. We combine technology and expert financial handling to manage your books day to day."
      },
      // 'name="description"'
    )
  }

  selectedIndex: number = 1

  selectChange(): void {
    console.log("Selected INDEX: " + this.selectedIndex)
  }
  SWIPE_ACTION = { LEFT: "swipeleft", RIGHT: "swiperight" }
  // Action triggered when user swipes
  swipe(selectedIndex: number, action = this.SWIPE_ACTION.RIGHT) {
    console.log("swipe")
    console.log("number", selectedIndex)
    console.log("action", action)
    // Out of range
    if (this.selectedIndex < 0 || this.selectedIndex > 1) return

    // Swipe left, next tab
    if (action === this.SWIPE_ACTION.LEFT) {
      const isLast = this.selectedIndex === 1
      this.selectedIndex = isLast ? 0 : this.selectedIndex + 1
      console.log("Swipe right - INDEX: " + this.selectedIndex)
    }

    // Swipe right, previous tab
    if (action === this.SWIPE_ACTION.RIGHT) {
      const isFirst = this.selectedIndex === 0
      this.selectedIndex = isFirst ? 1 : this.selectedIndex - 1
      console.log("Swipe left - INDEX: " + this.selectedIndex)
    }
  }
}
