import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  ChangeDetectorRef,
  AfterViewInit,
  OnDestroy
} from "@angular/core"
import { FormGroup, FormBuilder, FormControl, Validators } from "@angular/forms"
import { MatStepper, MatStep } from "@angular/material/stepper"
import { SignupService } from "../../../_services"
import { CustomErrorStateMatcher } from "../../../_helpers"
import { TIMEZONES, CURRENCIES, STREET_TYPES } from "../../../_mock"
import { ActivatedRoute, Router } from "@angular/router"
import { first } from "rxjs/operators"
import { environment } from "../../../../environments/environment"
import { BreakpointObserver, Breakpoints } from "@angular/cdk/layout"
import { Plan } from "../../../_models"
import { CouponCodeValidator } from "../../../_validators"
import { Subscription } from "rxjs"
import { StripeService, Elements, Element as StripeElement } from "ngx-stripe"

@Component({
  selector: "app-signup",
  templateUrl: "./signup.component.html",
  styleUrls: ["./signup.component.scss"]
})
export class SignupComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild("stepper") stepper: MatStepper
  @ViewChild("packageSelectionStep") packageSelectionStep: MatStep
  @ViewChild("cardNumberElement") cardNumberElement: ElementRef
  @ViewChild("cardExpiryElement") cardExpiryElement: ElementRef
  @ViewChild("cardCvcElement") cardCvcElement: ElementRef

  smallScreen: boolean

  paymentForm: FormGroup
  token: string
  loading = false
  submitted = false

  matcher = new CustomErrorStateMatcher()
  private dashboardUrl = environment.dashboardEndpoint

  isLinear = false
  hide = true
  planPackage: string = ""
  stepHeaderTitle: string = "Select a Package"
  stripePackages: Plan[] = []
  planIndex: any

  currencies = CURRENCIES
  timezones = TIMEZONES
  streetTypes = STREET_TYPES
  organizationFormGroup = this.signupService.organizationFormGroup

  coupon_code = new FormControl("", Validators.required)
  couponCodeData: any
  couponCodeDataSubscription: Subscription

  durationMapping: { [k: string]: string } = {
    "=0": "Invalid month.",
    "=1": "month",
    other: "months"
  }

  elements: Elements
  card: StripeElement
  cardNumber: any
  cardExpiry: any
  cardCvc: any
  cardHandler = this.onChange.bind(this)
  error: string

  constructor(
    private cd: ChangeDetectorRef,
    private signupService: SignupService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private breakpointObserver: BreakpointObserver,
    public couponCodeValidator: CouponCodeValidator,
    private stripeService: StripeService
  ) {
    this.breakpointObserver.observe([Breakpoints.XSmall, Breakpoints.Small]).subscribe(result => {
      this.smallScreen = result.matches
      setTimeout(() => {
        this.stepper.selectedIndex = 2
      }, 0)
    })

    this.couponCodeDataSubscription = this.signupService.couponCode.subscribe(data => {
      this.couponCodeData = data
    })
  }

  ngOnInit() {
    this.token = this.route.snapshot.queryParams["token"] || ""
    this.isLinear = !this.isLinear

    this.paymentForm = this.fb.group({
      name: [""]
    })

    this.signupService
      .getPackages()
      .pipe(first())
      .subscribe(plans => {
        this.stripePackages = plans["data"]
      })
  }

  ngAfterViewInit() {
    this.coupon_code.setAsyncValidators(
      this.couponCodeValidator.checkCouponCode.bind(this.couponCodeValidator)
    )

    const style = {
      base: {
        fontSmoothing: "antialiased",
        fontSize: "16px"
      }
    }

    this.stripeService.elements().subscribe(elements => {
      this.elements = elements

      if (!this.cardNumber) {
        this.cardNumber = this.elements.create("cardNumber", { style: style })
        this.cardNumber.mount(this.cardNumberElement.nativeElement)
        this.cardExpiry = this.elements.create("cardExpiry", { style: style })
        this.cardExpiry.mount(this.cardExpiryElement.nativeElement)
        this.cardCvc = this.elements.create("cardCvc", { style: style })
        this.cardCvc.mount(this.cardCvcElement.nativeElement)

        this.cardNumber.addEventListener("change", this.cardHandler)
      }
    })
  }

  ngOnDestroy() {
    if (this.cardNumber) {
      this.cardNumber.removeEventListener("change", this.cardHandler)
      this.cardNumber.destroy()
      this.cardExpiry.destroy()
      this.cardCvc.destroy()
    }
    this.couponCodeDataSubscription.unsubscribe()
  }

  get o(): any {
    return this.organizationFormGroup.controls
  }

  get p(): any {
    return this.paymentForm.controls
  }

  stepOnChange(event) {
    if (event.selectedIndex === 2) {
      this.stepHeaderTitle = "Select a Package"
    } else if (event.selectedIndex === 3) {
      this.stepHeaderTitle = "Enter Your Organization Details"
    } else if (event.selectedIndex === 4) {
      this.stepHeaderTitle = "Review Your Package Summary"
    } else if (event.selectedIndex === 5) {
      this.stepHeaderTitle = "Enter Your Payment Details"
    }
  }

  selectPlanPackage(planPackage: string) {
    this.planPackage = planPackage
    this.packageSelectionStep.completed = true

    this.planIndex = this.stripePackages.findIndex(x => {
      return x.stripe_plan.toLowerCase() === planPackage.toLowerCase()
    })

    this.stepper.next()
  }

  async onSubmit() {
    this.submitted = true

    await this.organizationFormGroup.patchValue({
      token: this.token,
      planPackage: this.planPackage,
      package_name: this.stripePackages[this.planIndex].name,
      couponCode: this.couponCodeData ? this.couponCodeData.id : ""
    })

    // stop here if form is invalid
    if (this.organizationFormGroup.invalid) {
      return
    }

    this.loading = true

    const name = this.paymentForm.get("name").value

    this.stripeService.createToken(this.cardNumber, { name }).subscribe(async result => {
      if (result.token) {
        // Use the token to create a charge or a customer
        // https://stripe.com/docs/charges

        this.error = null

        await this.organizationFormGroup.patchValue({
          stripeToken: result.token.id
        })

        await this.signupService
          .createSubscription(this.organizationFormGroup.value)
          .pipe(first())
          .subscribe(
            res => {
              window.location.href =
                this.dashboardUrl + "?status=Successfully subscribed, Please login to your account"
            },
            error => {
              console.log("error ", error)
              this.loading = false
            }
          )
      } else if (result.error) {
        // Error creating the token
        console.log(result.error.message)
        this.error = result.error.message
        this.loading = false
      }
    })
  }

  onChange({ error, brand }) {
    if (brand) {
      this.signupService.setBrandIcon(brand)
    }

    if (error) {
      this.error = error.message
    } else {
      this.error = null
    }
    this.cd.detectChanges()
  }

  removeCoupon() {
    this.signupService.clearCouponCode()
    this.coupon_code.patchValue("")
  }

  getTotalPrice() {
    if (this.couponCodeData && this.couponCodeData.amount_off) {
      return this.stripePackages[this.planIndex].cost - this.couponCodeData.amount_off / 100
    } else if (this.couponCodeData && this.couponCodeData.percent_off) {
      return (
        this.stripePackages[this.planIndex].cost -
        (this.couponCodeData.percent_off / 100) * this.stripePackages[this.planIndex].cost
      )
    }
    return this.stripePackages[this.planIndex].cost
  }
}
