import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';

@Component({
  selector: 'app-accounting',
  templateUrl: './accounting.component.html',
  styleUrls: ['./accounting.component.scss']
})
export class AccountingComponent implements OnInit {

  constructor(private meta: Meta, private title: Title) {
    //
  }

  ngOnInit() {
    this.setMetaTags()
  }

  setMetaTags() {
    this.title.setTitle("Online Accounting Service - NOAC Consultants")
    this.meta.updateTag(
      {
        name: "description",
        content:
          "Online Accounting Service from Kite. Find out how it works, how it helps businesses as we our solutions combines technology and expert accountant services to produce results."
      },
      // 'name="description"'
    )
  }

}
