import { Component, OnInit } from "@angular/core"
import { Title, Meta } from "@angular/platform-browser"

@Component({
  selector: "app-about",
  templateUrl: "./about.component.html",
  styleUrls: ["./about.component.scss"]
})
export class AboutComponent implements OnInit {
  constructor(private meta: Meta, private title: Title) {
    //
  }

  ngOnInit() {
    this.setMetaTags()
  }

  setMetaTags() {
    this.title.setTitle("About - NOAC Consultants")
    this.meta.updateTag(
      {
        name: "description",
        content:
          "Learn more about NOAC Consultants and the services we offer to the small and medium business community."
      },
      // 'name="description"'
    )
  }
}
